/**
 * AdminDashboard Component:
 * Renders the admin dashboard with various analytics and statistics.
 * Fetches data related to leads count, source analytics, and course analytics.
 * Displays charts and visual representations of the data using ApexCharts.
 * Provides information about different types of leads and their counts.
 * Shows top courses purchased and calls made using bar and line charts.
 * Presents source analytics in a bar chart.
 */

import {
  Box,
  Divider,
  HStack,
  Image,
  SimpleGrid,
  Spacer,
  Stack,
  Text,
} from "@chakra-ui/react";
import Chart from "react-apexcharts";
import { ApexOptions } from "apexcharts";
import totalleads from "../../icons/totalleads.svg";
import openleads from "../../icons/openleads.svg";
import finalleads from "../../icons/finalleads.svg";
import hotleads from "../../icons/hotleads.svg";
import unsubscribe from "../../icons/unsubscribe.svg";
import wronglead from "../../icons/wronglead.svg";
import enrolled from "../../icons/enrolled.svg";

import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getData } from "../../redux/actions/common.action";

// Define the shape of the analytics data
interface Analytics {
  leadscount: {
    totalLeads: number;
    openLeads: number;
    finalLeads: number;
    hotLeads: number;
    wrongLeads: number;
  }[];
  sourceanalytics: {
    _id: string;
    leads: number;
  }[];
  courseanalytics: {
    _id: string;
    count: number;
  }[];
}

const AdminDashboard = () => {
  // Retrieve data from the Redux store
  const { leadscount, sourceanalytics, courseanalytics } = useSelector(
    (state: { common: Analytics }) => state.common
  );

  const dispatch: any = useDispatch();

  // Fetch data from the API on component mount
  useEffect(() => {
    dispatch(getData("dashboard/leads-count"));
    dispatch(getData("dashboard/analytics"));
    dispatch(getData("dashboard/source"));
  }, [dispatch]);

  // Extract categories and data for course analytics
  const categoriestoshow = courseanalytics
    ?.map((item) => item._id)
    .filter((id) => id !== null);
  const categoriesdatacourse = courseanalytics?.map((item) => item.count);

  // Extract categories and data for source analytics
  const categoriestoshowSource = sourceanalytics
    ?.map((item) => item._id)
    .filter((id) => id !== null);
  const categoriesdatasource = sourceanalytics?.map((item) => item.leads);

  // Calculate the total count for course analytics
  const totalCount = categoriesdatacourse.reduce(
    (total, count) => total + count,
    0
  );

  // Calculate the percentage for each count value in course analytics
  const percentages = categoriesdatacourse.map(
    (count) => (count / totalCount) * 100
  );

  const leadsData = [
    {
      icons: totalleads,
      name: "Total Leads",
      amount: 13258000,
      numofLeads: leadscount && leadscount[0]?.totalLeads,
      color: "#37AE00",
    },
    {
      icons: openleads,
      name: "Open Leads",
      amount: 13258000,
      numofLeads: leadscount && leadscount[0]?.openLeads,
      color: "#E27A00",
    },
    {
      icons: finalleads,
      name: "Final Leads",
      amount: 13258000,
      numofLeads: leadscount && leadscount[0]?.finalLeads,
      color: "#1243A0",
    },
    {
      icons: hotleads,
      name: "Hot Leads",
      amount: 13258000,
      numofLeads: leadscount && leadscount[0]?.hotLeads,
      color: "#5B5B60",
    },
    {
      icons: unsubscribe,
      name: "Unsubscribed",
      amount: 13258000,
      numofLeads: 0,
      color: "#FFBF00",
    },
    {
      icons: wronglead,
      name: "Wrong Leads",
      amount: 13258000,
      numofLeads: leadscount && leadscount[0]?.wrongLeads,
      color: "#FF0000",
    },
    {
      icons: enrolled,
      name: "Enrolled",
      amount: 13258000,
      numofLeads: 5000,
      color: "#8580C2",
    },
  ];

  const [data, setData] = useState([
    75, 90, 60, 85, 70, 80, 95, 75, 90, 60, 85, 70, 80,
  ]);

  const [datas, setDatas] = useState([
    75, 90, 60, 85, 70, 80, 95, 75, 90, 60, 85, 70, 80,
  ]);
  const [categories, setCategories] = useState([]);

  const colors: any = ["#ff0000", "#00ff00", "#0000ff", "#ffff00", "#ff00ff"];
  let curve: any = "smooth";

  const options = {
    chart: {
      id: "horizontal-bar",
      toolbar: {
        show: false,
      },
    },
    plotOptions: {
      bar: {
        horizontal: true,
        distributed: true,
      },
    },
    xaxis: {
      categories: categoriestoshow,
    },
    yaxis: {
      type: "logarithmic",
      labels: {
        style: {
          fontWeight: "bold",
        },
      },
    },
    colors: [
      "#33b2df",
      "#546E7A",
      "#d4526e",
      "#13d8aa",
      "#A5978B",
      "#2b908f",
      "#f9a3a4",
      "#90ee7e",
      "#f48024",
      "#69d2e7",
    ],
  };

  const options2: ApexOptions = {
    chart: {
      type: "line",
      height: 350,
      toolbar: {
        show: false,
      },
    },
    stroke: {
      width: 3,
      curve: "smooth",
      colors: ["#ff9800"], // Orange color
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: "55%",
        colors: colors,
      },
    },
    dataLabels: {
      enabled: false,
    },
    xaxis: {
      categories: categories,
    },
    yaxis: {
      labels: {
        style: {
          fontWeight: "bold",
        },
      },
    },
  };

  const series = [
    {
      data: categoriesdatacourse,
    },
  ];

  const series_source = [
    {
      data: categoriesdatasource,
    },
  ];

  const options3: ApexOptions = {
    chart: {
      type: "bar",
      height: 350,
      toolbar: {
        show: false,
      },
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: "40%",
        distributed: true,
      },
    },
    dataLabels: {
      enabled: false,
    },
    xaxis: {
      categories: categoriestoshowSource,
    },
    yaxis: {
      labels: {
        style: {
          fontWeight: "bold",
        },
      },
    },
    colors: [
      "#d4526e",
      "#546E7A",
      "#13d8aa",
      "#A5978B",
      "#2b908f",
      "#33b2df",
      "#90ee7e",
      "#f48024",
      "#f9a3a4",
      "#69d2e7",
    ],
  };

  return (
    <Stack>
      {/* Displaying the leads data */}
      <SimpleGrid
        columns={{ base: 2, sm: 2, md: 3, lg: 4 }}
        gap="0.5rem"
        bg="gray.200"
        pb="2"
      >
        {leadsData?.map((el, i) => (
          <Box
            h="3.7rem"
            px="4"
            py="1"
            borderRadius={"0.25rem"}
            bg="whiteAlpha.900"
          >
            <HStack>
              <Box>
                <Text
                  fontSize={"0.9rem"}
                  padding={1}
                  fontWeight={"800"}
                  color={el.color}
                >
                  â‚¹ {el.amount}
                </Text>
                <Text fontSize={"0.95rem"} fontWeight={600}>
                  {el.name}{" "}
                  <span style={{ fontWeight: "400", marginTop: "8px" }}>
                    {el.numofLeads}
                  </span>{" "}
                </Text>
              </Box>
              <Spacer />
              <Box mt="0">
                <Image
                  mt={"0"}
                  borderRadius="full"
                  boxSize="40px"
                  src={el.icons}
                  alt="error Icons"
                />
              </Box>
            </HStack>
          </Box>
        ))}
      </SimpleGrid>

      {/* Displaying top courses purchased */}
      <Stack py="1" mt="2" bg="whiteAlpha.900" borderRadius={"0.25rem"}>
        <SimpleGrid columns={{ sm: 1, md: 2 }} gap="0.8rem" px="2">
          <Box bg="gray.100" p="1" borderRadius={"5"}>
            <Text ml="4" mb="-2" fontSize={"1rem"} fontWeight={700}>
              Top Courses Purchased
            </Text>
            <Chart
              options={options}
              series={series}
              type="bar"
              height="370"
              width="100%"
            />
          </Box>
          <Box bg="gray.100" p="1" borderRadius={5}>
            <Text ml="4" mb="-2" fontSize={"1rem"} fontWeight={700}>
              Calls Made
            </Text>
            <Chart
              options={options2}
              series={series}
              type="line"
              height="400"
              width="100%"
            />
          </Box>
        </SimpleGrid>
        <Divider />
        <Box>
          <Text ml="4" mb="-2" fontSize={"1rem"} fontWeight={700}>
            Source
          </Text>
          <Chart
            options={options3}
            series={series_source}
            type="bar"
            height="400"
            width="100%"
          />
        </Box>
      </Stack>
    </Stack>
  );
};

export default AdminDashboard;
